import React, { FC } from 'react';
import { FontWeight, ThemeTypographyKeys, TypographyDefinition } from '../../../libs/theme/create-theme';
import { IntrinsicElementProps } from '../../../types/utility';
import { useTheme } from '../../theme-provider';

type TypographyTags = 'h1' | 'h2' | 'h3' | 'h4' | 'h5' | 'h6' | 'p';

interface TypographyProps extends IntrinsicElementProps<TypographyTags> {
  weight?: FontWeight;
  color?: string;
  tagOverride?: string;
}

function buildTypographyStyle(config: TypographyDefinition, weight?: string, color?: string) {
  const typographyStyle: React.CSSProperties = {
    fontWeight: (weight as any) ?? config.defaultFontWeight ?? 'normal',
    fontSize: config.fontSize,
    lineHeight: config.lineHeight,
    color: color ?? 'inherit',
  };
  if (config.textTransform) typographyStyle.textTransform = config.textTransform as any;
  if (config.letterSpacing) typographyStyle.letterSpacing = config.letterSpacing;
  if (config.fontFamily) typographyStyle.fontFamily = config.fontFamily;

  return typographyStyle;
}

function buildTypographyComponent(
  Tag: TypographyTags,
  size: 'small' | 'medium' | 'large' | null = null,
  themeKey: ThemeTypographyKeys | null = null,
): FC<TypographyProps> {
  return ({ children, weight, color, tagOverride, style = {}, ...rest }: TypographyProps) => {
    const theme = useTheme();
    const key = themeKey ?? Tag ?? 'h1';
    const config = size ? theme.typography[key as 'p' | 'mono'][size] : theme.typography[key as 'h1'];
    const TypographyTag = (tagOverride as TypographyTags) ?? Tag;
    return (
      <TypographyTag
        {...rest}
        style={{
          ...buildTypographyStyle(config, weight, color),
          ...style,
        }}
      >
        {children}
      </TypographyTag>
    );
  };
}

export const H1 = /* @__PURE__ */ buildTypographyComponent('h1');
export const H2 = /* @__PURE__ */ buildTypographyComponent('h2');
export const H3 = /* @__PURE__ */ buildTypographyComponent('h3');
export const H4 = /* @__PURE__ */ buildTypographyComponent('h4');
export const H5 = /* @__PURE__ */ buildTypographyComponent('h5');
export const H6 = /* @__PURE__ */ buildTypographyComponent('h6');
export const BodyLarge = /* @__PURE__ */ buildTypographyComponent('p', 'large');
export const BodyMedium = /* @__PURE__ */ buildTypographyComponent('p', 'medium');
export const BodySmall = /* @__PURE__ */ buildTypographyComponent('p', 'small');
export const MonoLarge = /* @__PURE__ */ buildTypographyComponent('p', 'large', 'mono');
export const MonoMedium = /* @__PURE__ */ buildTypographyComponent('p', 'medium', 'mono');
export const MonoSmall = /* @__PURE__ */ buildTypographyComponent('p', 'small', 'mono');

H1.displayName = 'Typography.H1';
